:root {
  --darkGreen: hsl(170, 100%, 17.8%);
  --lightGreen: hsl(107, 46.6%, 50.8%);
  --blue: hsl(212, 91.5%, 27.8%);
  --red: hsl(352, 85.6%, 59.2%);
  --gold: hsl(49, 100%, 51%);
  --yellow: hsl(51, 100%, 54.5%);
  --black: hsl(0, 0%, 0%);
  --grey: hsl(0, 0%, 50.2%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-body: "Lato", sans-serif;
  --font-display: "League Spartan", sans-serif;
}

.btn__contact a, .btn__login a {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0.625rem 1.25rem;
  color: var(--nwcGreen);
  background-color: var(--white);
  border: 0.0625rem solid var(--white);
  border-radius: 0.3125rem;
  margin-left: 1.25rem;
  cursor: pointer;
}
@media (max-width: 59.999375em) {
  .btn__contact a, .btn__login a {
    margin-left: 0;
  }
}
.btn__contact a:hover, .btn__login a:hover {
  background-color: var(--nwcLightGreen);
  border: 0.0625rem solid var(--nwcLightGreen);
  color: var(--white);
  transition: color 250ms ease-in-out;
}
.btn__center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn__rsvp {
  font-family: var(--font-display);
  font-size: 1.125rem;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0.625rem 1.25rem;
  color: var(--white);
  background-color: var(--lightGreen);
  border: 0.0625rem solid var(--lightGreen);
  border-radius: 0.3125rem;
  cursor: pointer;
}
.btn__rsvp:hover {
  background-color: var(--white);
  border: 0.0625rem solid var(--white);
  color: var(--darkGreen);
  text-decoration: none;
  transition: color 250ms ease-in-out;
}

.is-style-fill:hover {
  background-color: var(--lightGreen);
  color: var(--white);
  cursor: pointer;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-weight: 500;
  line-height: 1.3;
  background-color: var(--darkGreen);
}

.wrapper {
  max-width: 75rem;
  margin: 17.1875rem auto 0;
  padding: 0 0.625rem;
  text-align: center;
}

.row {
  padding: 0 0.625rem;
}

.wp-block-columns {
  display: flex;
  margin-bottom: 1.75em;
  box-sizing: border-box;
  flex-wrap: wrap !important;
  align-items: normal !important;
  width: 100%;
}

@media (max-width: 43.74875em) {
  .wp-container-2 .wp-block-column {
    margin-bottom: 2.5rem;
  }
}

img {
  max-width: 100%;
}

.content {
  width: 100%;
  text-align: center;
  margin-top: 2em;
  margin-bottom: 1.625em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.content-form {
  width: 100%;
  text-align: left;
  margin-top: 2em;
  margin-bottom: 1.625em;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.rsvp_head {
  margin-bottom: 2.5rem;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  font-family: var(--font-display);
  line-height: 1.1;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  font-family: var(--font-body);
  font-size: 1.125rem;
  line-height: 1.5;
  color: var(--white);
  margin-top: 0.625rem;
  margin-bottom: 2.5rem;
}

h1 {
  font-size: 3rem;
  color: var(--white);
  margin: 1.25rem 0 1.875rem;
}

h2 {
  font-size: 2.625rem;
  color: var(--lightGreen);
}

strong {
  font-weight: 700;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

a {
  color: var(--white);
}

.headingThanks {
  font-size: 1.6875rem;
  font-weight: 700;
  color: var(--lightGreen);
  margin-bottom: 0.625rem;
}

.thankYou {
  font-size: 2.25rem;
  font-weight: 700;
  color: var(--lightGreen);
  margin-bottom: 0.625rem;
}

.topBar {
  background-image: url("img/topBar.jpg");
  width: 100%;
  height: 20px;
  top: 0;
}

.header h1 {
  color: var(--white);
  font-weight: 500;
  font-size: 2.25rem;
}

#titleLink {
  background-color: var(--black);
  text-align: center;
  max-height: 10.9375em;
  padding: 1.25em 0 1.875em;
  margin-bottom: 1.25em;
}

#titleLink h1 {
  color: var(--lightGreen);
}

#titleLink img {
  max-width: 100%;
  margin-top: 1.25em;
}

.titleImg {
  margin-left: auto;
  margin-right: auto;
}

header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
  background-color: var(--black);
}

.headerLogo {
  margin-bottom: 2.5rem;
}

div.wpforms-container-full .wpforms-form .wpforms-field-label {
  font-family: var(--font-body);
  font-weight: 700;
  font-size: 1rem;
  color: var(--white);
  display: block;
  float: none;
  line-height: 1.3;
  margin: 0 0 0.625rem 0;
  padding: 0;
  word-break: break-word;
  word-wrap: break-word;
}

div.wpforms-container-full .wpforms-form input[type=submit],
div.wpforms-container-full .wpforms-form button[type=submit],
div.wpforms-container-full .wpforms-form .wpforms-page-button {
  background-color: var(--lightGreen);
  border: 1px solid var(--lightGreen);
  color: var(--white);
  font-size: 1em;
  padding: 10px 15px;
  cursor: pointer;
  padding: 0.625rem 1.25rem;
  margin-top: 1.25rem;
}

div.wpforms-container-full .wpforms-form input[type=submit]:hover,
div.wpforms-container-full .wpforms-form input[type=submit]:focus,
div.wpforms-container-full .wpforms-form input[type=submit]:active,
div.wpforms-container-full .wpforms-form button[type=submit]:hover,
div.wpforms-container-full .wpforms-form button[type=submit]:focus,
div.wpforms-container-full .wpforms-form button[type=submit]:active,
div.wpforms-container-full .wpforms-form .wpforms-page-button:hover,
div.wpforms-container-full .wpforms-form .wpforms-page-button:active,
div.wpforms-container-full .wpforms-form .wpforms-page-button:focus {
  background-color: var(--white);
  border: 1px solid var(--white);
  color: var(--darkGreen);
  cursor: pointer;
}

form#wpforms-form-47 .wpforms-field-divider h3,
form#wpforms-form-93 .wpforms-field-divider h3 {
  color: var(--lightGreen);
  font-size: 2rem;
  text-align: left;
  text-transform: uppercase;
  margin-top: 3.75rem;
}

.appetizer {
  color: var(--white);
  margin-top: 1.25rem;
}

.footer__info {
  text-align: center;
  margin-top: 3.75rem;
}
.footer__info p {
  font-size: 0.875rem;
}
.footer__info a {
  text-decoration: underline;
  cursor: pointer;
}
.footer__info a:hover {
  color: var(--lightGreen);
}